import { FadeInUp, ScaleIn } from './animations'

export default function Hero() {
  return (
    <section className="relative bg-gray-900 py-24 px-4 overflow-hidden">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-30">
        <div className="absolute inset-0 bg-gradient-to-r from-gray-800 to-gray-900"></div>
        <div className="absolute inset-0 bg-[radial-gradient(circle_at_2px_2px,rgba(255,255,255,0.05)_1px,transparent_0)] bg-[length:30px_30px]"></div>
      </div>

      <div className="max-w-7xl mx-auto text-center relative z-10">
        <div className="mb-12">
          <ScaleIn delay={200} className="relative inline-block mb-8">
            <img
              src="/Hussien.JPG"
              alt="حسين"
              className="w-40 h-40 rounded-full mx-auto object-cover border-4 border-white dark:border-gray-700 shadow-2xl hover:scale-105 transition-transform duration-300"
            />
            <div className="absolute inset-0 rounded-full bg-gradient-to-r from-blue-500/20 to-purple-500/20 dark:from-blue-400/30 dark:to-purple-400/30"></div>
          </ScaleIn>

          <div className="space-y-4 mb-8">
            <FadeInUp delay={400}>
              <h1 className="text-5xl md:text-7xl font-bold bg-gradient-to-r from-gray-100 via-gray-200 to-gray-300 bg-clip-text text-transparent leading-tight">
                Hi, I&apos;m{' '}
                <span className="bg-gradient-to-r from-gray-200 via-gray-300 to-gray-400 bg-clip-text text-transparent">
                  Hussein
                </span>
              </h1>
            </FadeInUp>
            <FadeInUp delay={600}>
              <p className="text-xl md:text-2xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
                A web developer specializing in creating{' '}
                <span className="font-semibold text-gray-200">exceptional digital experiences</span>{' '}
                and responsive solutions for clients
              </p>
            </FadeInUp>
            <FadeInUp delay={800}>
              <p className="text-lg text-gray-400 max-w-2xl mx-auto leading-relaxed">
                Alpha Team Leader • I love turning ideas into reality using the latest technologies and tools
              </p>
            </FadeInUp>
          </div>
        </div>

        <FadeInUp delay={1000} className="flex flex-col sm:flex-row gap-6 justify-center mb-16">
          <a
            href="/projects"
            className="group relative bg-gradient-to-r from-red-600 to-red-700 text-white px-10 py-4 rounded-xl font-semibold text-lg hover:from-red-500 hover:to-red-600 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:-translate-y-1"
          >
            <span className="relative z-10">View My Work</span>
            <div className="absolute inset-0 bg-gradient-to-r from-red-500 to-red-600 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
          </a>
          <a
            href="/contact"
            className="group relative border-2 border-red-600 text-red-300 px-10 py-4 rounded-xl font-semibold text-lg hover:border-red-500 hover:text-red-200 hover:bg-red-500/10 transition-all duration-300 shadow-md hover:shadow-lg transform hover:-translate-y-1 bg-gray-800/80 backdrop-blur-sm"
          >
            <span className="relative z-10">Contact Me</span>
          </a>
        </FadeInUp>

        <FadeInUp delay={1200} className="flex justify-center space-x-8 rtl:space-x-reverse">
          {[
            { href: 'https://github.com', icon: 'M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z', label: 'GitHub' },
            { href: 'https://linkedin.com', icon: 'M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z', label: 'LinkedIn' },
            { href: 'mailto:hussein@example.com', icon: 'M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z', label: 'Email' }
          ].map((social, index) => (
            <ScaleIn key={index} delay={1200 + (index * 100)} className="group relative p-3 bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm rounded-xl shadow-md hover:shadow-lg transition-all duration-300 hover:scale-110 border border-gray-200/50 dark:border-gray-700/50">
              <svg className="w-6 h-6 text-gray-400 group-hover:text-red-400 transition-colors duration-300" fill="currentColor" viewBox="0 0 24 24">
                <path d={social.icon}/>
              </svg>
              <div className="absolute inset-0 bg-gradient-to-r from-red-500/10 to-red-600/10 dark:from-red-400/20 dark:to-red-500/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </ScaleIn>
          ))}
        </FadeInUp>
      </div>
    </section>
  )
}
